
$border: 0.2rem;
@import "index";

.form-container {
    width: 60%;
    padding: 2rem 0 0;
    margin: 2rem auto 0;
    display: block;

    .recipe-form {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: $border;
        padding: 2rem 0 0;

        .h2-recipe {
            margin: 1rem 0;
            letter-spacing: 0.1rem;
        }

        .h5-recipe {
            width: 80%;
            margin: 0 auto;
            align-self: flex-start;
        }
        .recipe-label, .smaller-label {
            width: 80%;
            padding: 0.3rem;

            .space-top {
                margin-top: 0.3rem;
            }
            .recipe-input,
            textarea {
                width: 100%;
                height: 2.5rem;
                padding: 1ch;
                margin: 0.3rem 0;
                border-radius: $border;
                border: 1px solid black;
                font-size: 1.1rem;
            }
            .recipe-textarea {
                height: auto;
            }
            .img-file {
                padding: 0;
                background-color: white;
                height: inherit;
                cursor: pointer;
            }
        }
        .smaller-label {
            display: flex;
            flex-direction: column;
            justify-content: center;
            
            .recipe-input {
                width: 40%;
            }
        }
        .list-label {
            width: 80%;
            padding: 0.3rem;
            
            .inline-input {
                display: flex;
                justify-content: space-between;
                
                .recipe-input {
                    width: 100%;
                    padding: 1ch;
                    margin-right: 0.5rem;
                    border-radius: $border;
                    border: 1px solid black;
                    font-size: 1.1rem;
                }
                .form-list {
                    text-align: left;
                }
                .add-list {
                    padding: 0 1rem;
                    color: white;
                    background-color: black;
                    font-weight: bold;
                }
            }
        }
        .recipe-btn {
            width: 10rem;
            height: 2.8rem;
            margin: 1rem;
            font-weight: bold;
            color: white;
            background-color: black;
        }
        span {
            cursor: pointer;
            margin: 0 0.5rem;
        }
    }
}

