@media screen and (max-width: 768px) {
    .form-container {
        width: 100%;
        margin: 1rem 0;

        .recipe-form {
            .smaller-label {
                .recipe-input {
                    width: 50%;
                }
            }
        }
    }
}

@media screen and (max-width: 1100px) and (min-width: 769px) {
    .form-container {
        width: 80%;
    }
}